import React from 'react';
import './App.css';
import NavTabs from "./components/TabBar.js";
import Footer from "./components/Footer.js";
//import Box from '@material-ui/core/Box';
//<PageNavbar />
import {
  HashRouter as Router,
  Switch,
  Route,
  Link,
} from "react-router-dom";
import Home from './components/Home.js';
import Organizations from './components/Organizations.js'
import Students from './components/Students.js'
import About from './components/About.js'
import Research from './components/Research.js'
import Internship from './components/Internship.js'
import MachineLearning from './components/MachineLearning.js'

function App() {
  return (
    <>
      <Router basename={process.env.PUBLIC_URL}>
        <NavTabs></NavTabs>
        <br></br>
        <br></br>
        <br></br>
        <br></br>
        <Switch>
          <Route path="/mlpast" component={MachineLearning}>
          </Route>
          <Route path="/research" component={Research}>
          </Route>
          <Route path="/internship" component={Internship}>
          </Route>
          <Route path="/students" component={Students}>
          </Route>
          <Route path="/about" component={About}>
          </Route>
          <Route path="/organizations" component={Organizations}>
          </Route>
          <Route exact path="/">
            <Home />
          </Route>
        </Switch>
        </Router>
        <Footer></Footer>

    </>
  );
}

export default App;
